import React, {useState} from 'react';
import {Time} from "./components/Time";
import {Place} from "./components/Place";
import {Action} from "./components/Action";
import {Button} from "./components/Button";
import {HashRouter, Route} from "react-router-dom";
import {ModeButton} from "./components/ModeButton";
import {Quests} from "./components/Quests";

function App() {
    const [times,] = useState([
        30, 60, 90, 120
    ]);
    const [places,] = useState([
        "–ì—É–±—ã üíã", "–®–µ—é", "–°–ø–∏–Ω—É", "–ì—Ä—É–¥—å", "–ñ–∏–≤–æ—Ç", "–ü–æ–ø—É", "–ì—É–±—ã", "–í–æ–∫—Ä—É–≥", "–ë–µ–¥—Ä–∞", "–°—Ç—É–ø–Ω–∏", "–ö–ª–∏—Ç–æ—Ä", "–ì—É–±—ã", "–ö–ª–∏—Ç–æ—Ä", "–ö–ª–∏—Ç–æ—Ä", "–ö–ª–∏—Ç–æ—Ä", "–ö–ª–∏—Ç–æ—Ä", "–ö–ª–∏—Ç–æ—Ä"
    ]);
    const [actions,] = useState([
        "–õ–∏–∑–∞—Ç—å", "–°–æ—Å–∞—Ç—å", "–ö—É—Å–∞—Ç—å", "–¶–µ–ª–æ–≤–∞—Ç—å", "–ì–ª–∞–¥–∏—Ç—å", "–©–∏–ø–∞—Ç—å", "–ó–∞—Å–æ—Å", "–í—Å—Ç–∞–≤–∏—Ç—å", "–°–º–æ—Ç—Ä–µ—Ç—å",
    ]);

    const [placesTwo,] = useState([
        "–ì—É–±—ã", "–®–µ—é", "–°–ø–∏–Ω—É", "–ì—Ä—É–¥—å", "–ñ–∏–≤–æ—Ç", "–ü–æ–ø—É", "–ì—É–±—ã", "–í–æ–∫—Ä—É–≥", "–ë–µ–¥—Ä–∞", "–ü–ª–µ—á–∏", "–ß–ª–µ–Ω", "–Ø–∏—á–∫–∏"
    ]);
    const [actionsTwo,] = useState([
        "–õ–∏–∑–∞—Ç—å", "–°–æ—Å–∞—Ç—å", "–ö—É—Å–∞—Ç—å", "–¶–µ–ª–æ–≤–∞—Ç—å", "–ì–ª–∞–¥–∏—Ç—å", "–©–∏–ø–∞—Ç—å", "–ó–∞—Å–æ—Å", "–í—Å—Ç–∞–≤–∏—Ç—å", "–°–º–æ—Ç—Ä–µ—Ç—å"
    ]);
    const [quests,] = useState([
        "–ü–∞—Ä–µ–Ω—å –ª–µ–∂–∏—Ç –Ω–∞ —Å–ø–∏–Ω–µ. –î–µ–≤—É—à–∫–∞ –º–µ–¥–ª–µ–Ω–Ω–æ –ø—Ä–æ–≤–æ–¥–∏—Ç—å –≥—Ä—É–¥—å—é —Å –Ω–æ–≥ –¥–æ –≥–æ–ª–æ–≤—ã –ø–æ –ø–∞—Ä–Ω—é, –∞ –≤ –∫–æ–Ω—Ü–µ –ø–æ –æ—á–µ—Ä–µ–¥–∏ –¥–∞–µ—Ç –ø–∞—Ä–Ω—é —Ü–µ–ª–æ–≤–∞—Ç—å –¥–≤–∞ —Å–æ—Å–æ—á–∫–∞", "–ü–∞—Ä–µ–Ω—å –ø–æ–ª—Ç–æ—Ä—ã (–¥–≤–µ) –º–∏–Ω—É—Ç—ã –ª–∞—Å–∫–∞–µ—Ç, —Ü–µ–ª—É–µ—Ç —Å—Ç—É–ø–Ω–∏ –¥–µ–≤—É—à–∫–∏", "–ü–∞—Ä–µ–Ω—å –¥–≤–µ –º–∏–Ω—É—Ç—ã –æ–±–ª–∏–∑—ã–≤–∞–µ—Ç, –∫—É—Å–∞–µ—Ç, —Ü–µ–ª—É–µ—Ç –ø–æ–ø–∫—É –¥–µ–≤—É—à–∫–∏",
        "–ü–∞—Ä–∞ –ø–æ–¥—Ö–æ–¥–∏—Ç –∫ –∑–µ—Ä–∫–∞–ª—É –∏ —Ü–µ–ª—É–µ—Ç—Å—è –Ω–∞ –ø—Ä–æ—Ç—è–∂–µ–Ω–∏–∏ ~1 –º–∏–Ω—É—Ç—ã", "–í–∑—è—Ç—å –¥–µ–≤—É—à–∫—É –¥–≤—É–º—è —Ä—É–∫–∞–º–∏ –∑–∞ –≥—Ä—É–¥—å, –∏ 10 —Ä–∞–∑ –ø—Ä–æ–Ω–∏–∫–Ω—É—Ç—å —è–∑—ã–∫–æ–º –≤ –≤–∞–≥–∏–Ω—É",
        "–î–µ–≤—É—à–∫–∞ –Ω–∞ –ø—Ä–æ—Ç—è–∂–µ–Ω–∏–∏ 30 —Å–µ–∫—É–Ω–¥ –≤–æ–¥–∏—Ç –≥—Ä—É–¥—å—é –ø–æ —Å–ø–∏–Ω–µ –ø–∞—Ä–Ω—è", "–î–µ–≤—É—à–∫–∞ —Å–∞–¥–∏—Ç—Å—è –Ω–∞ —Å—Ç—É–ª –∏ –∑–∞–∫–∏–¥—ã–≤–∞–µ—Ç –Ω–æ–≥–∏ –Ω–∞ –º—É–∂—á–∏–Ω—É. –ú—É–∂—á–∏–Ω–∞ 2 –º–∏–Ω—É—Ç—ã –¥–µ–ª–∞–µ—Ç –∫—É–Ω–∏",
        "–ü–∞—Ä–µ–Ω—å –ª–µ–∂–∏—Ç –Ω–∞ —Å–ø–∏–Ω–µ. –î–µ–≤—É—à–∫–∞ –º–µ–¥–ª–µ–Ω–Ω–æ –ø—Ä–æ–≤–æ–¥–∏—Ç—å –≥—Ä—É–¥—å—é —Å –Ω–æ–≥ –¥–æ –≥–æ–ª–æ–≤—ã –ø–æ –ø–∞—Ä–Ω—é, –∞ –≤ –∫–æ–Ω—Ü–µ –ø–æ –æ—á–µ—Ä–µ–¥–∏ –¥–∞–µ—Ç –ø–∞—Ä–Ω—é —Ü–µ–ª–æ–≤–∞—Ç—å –¥–≤–∞ —Å–æ—Å–æ—á–∫–∞.",
        "–ü–∞—Ä–µ–Ω—å –ª–æ–∂–∏—Ç—Å—è –Ω–∞ —Å–ø–∏–Ω—É. –î–µ–≤—É—à–∫–∞ –≤–æ–¥–∏—Ç –ø–æ –ª–∏—Ü—É –ø–∞—Ä–Ω—è —Å–≤–æ–µ–π –≤–∞–≥–∏–Ω–æ–π –∫–∞–∫ –∑–∞—Ö–æ—á–µ—Ç –Ω–∞ –ø—Ä–æ—Ç—è–∂–µ–Ω–∏–∏ 2 –º–∏–Ω—É—Ç",
        "–î–µ–≤—É—à–∫–∞ —Ü–µ–ª—É–µ—Ç –ø–∞—Ä–Ω—è –æ—Ç –≥—É–± –¥–æ —á–ª–µ–Ω–∞ –Ω–∞ –ø—Ä–æ—Ç—è–∂–µ–Ω–∏–∏ 1 –º–∏–Ω—É—Ç—ã",
        "–î–µ–≤—É—à–∫–∞ –ø—Ä–æ–≥–∏–±–∞–µ—Ç —Å–ø–∏–Ω—É –∏ –ø—ã—Ç–∞–µ—Ç—Å—è –ø–æ–ª–Ω–æ—Å—Ç—å—é –ø–æ–∫–ª–∞—Å—Ç—å –≥—Ä—É–¥—å –Ω–∞ –∫—Ä–æ–≤–∞—Ç—å. –ú—É–∂—á–∏–Ω–∞ –≤–≤–æ–¥–∏—Ç –ø–∞–ª–µ—Ü –∏–ª–∏ –ø–∞–ª—å—Ü—ã –≤ –¥–µ–≤—É—à–∫—É –∏ –º–∏–Ω—É—Ç—É –¥–µ–ª–∞–µ—Ç –∫—Ä—É–≥–æ–≤—ã–µ –¥–≤–∏–∂–µ–Ω–∏—è –≤–Ω—É—Ç—Ä–∏",
        "–î–µ–≤—É—à–∫–∞ –ª–æ–∂–∏—Ç—Å—è –Ω–∞ –∫—Ä–∞–π –∫—Ä–æ–≤–∞—Ç–∏. –ü–∞—Ä–µ–Ω—å —Å–∞–¥–∏—Ç—Å—è –Ω–∞ –∫–æ–ª–µ–Ω–∏ –ø–µ—Ä–µ–¥ –∫—Ä–æ–≤–∞—Ç—å—é. –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ —à–∏—Ä–æ–∫–æ —Ä–∞–∑–¥–≤–∏–≥–∞–µ—Ç –Ω–æ–≥–∏ –¥–µ–≤—É—à–∫–∏ –≤ —à–ø–∞–≥–∞—Ç –∏ –¥–µ–ª–∞–µ—Ç –∫—É–Ω–∏ –Ω–∞ –ø—Ä–æ—Ç—è–∂–µ–Ω–∏–∏ –ø–æ–ª—Ç–æ—Ä—ã –º–∏–Ω—É—Ç—ã"
    ])

    const [chosenTime, setChosenTime] = useState(0);
    const [chosenPlace, setChosenPlace] = useState("Hot");
    const [chosenAction, setChosenAction] = useState("Hot");

    const [chosenTimeSecond, setChosenTimeSecond] = useState(0);
    const [chosenPlaceSecond, setChosenPlaceSecond] = useState("Hot");
    const [chosenActionSecond, setChosenActionSecond] = useState("Hot");

    const [chosenQuest, setChosenQuest] = useState("–ù–∞–∂–º–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É");
    const [increment, setIncrement] = useState(0);

    const playFirst = () => {
        setChosenTime(times[Math.floor(Math.random() * times.length)]);
        setChosenPlace(places[Math.floor(Math.random() * places.length)]);
        setChosenAction(actions[Math.floor(Math.random() * actions.length)]);
    }

    const playSecond = () => {
        setChosenTimeSecond(times[Math.floor(Math.random() * times.length)]);
        setChosenPlaceSecond(placesTwo[Math.floor(Math.random() * places.length)]);
        setChosenActionSecond(actionsTwo[Math.floor(Math.random() * actions.length)]);
    }

    const [isBackground, setIsBackground] = useState(false);

    const playQuest = () => {
        if (increment >= quests.length - 1) {
            setIncrement(0);
            setChosenQuest(quests[increment])
        } else {
            setChosenQuest(quests[increment])
            setIncrement(increment + 1);
        }
    }
    return (
        <>
            <HashRouter>
                <Route exact path="/">
                    <div className={isBackground ? "with-background" : null}>
                        <div style={{marginTop: "-17px"}}
                             className={"container"}>
                            <Time chosenTime={chosenTime}/>
                            <div className={"main-container"}>
                                <Place chosenPlace={chosenPlace}/>

                                <Action chosenAction={chosenAction}/>
                            </div>
                            <Button play={playFirst}/>
                        </div>

                        <div style={{
                            borderTop: "1px solid #0000002e",
                            paddingTop: '50px',
                            marginBottom: "0px"
                        }}
                             className={isBackground ? "container second-with-bg" : "container second"}>
                            <Time chosenTime={chosenTimeSecond}/>
                            <div className={"main-container"}>
                                <Place chosenPlace={chosenPlaceSecond}/>
                                <Action chosenAction={chosenActionSecond}/>
                            </div>
                            <Button play={playSecond}/>
                            <ModeButton/>
                            <div className={"bg-button-container"}>
                                <span className={"bg-button"}
                                      onClick={() => setIsBackground(!isBackground)}>Background</span>
                            </div>
                        </div>
                    </div>
                </Route>
                <Route path="/quests">
                    <Quests playQuest={playQuest} quest={chosenQuest}/>
                </Route>
            </HashRouter>
        </>
    );
}

export default App;
