import React from 'react';

export const Time = ({chosenTime}) => {
    let init = 0;
    if (chosenTime  === 30) {
        init = "30 —Å–µ–∫—É–Ω–¥ "
    }else if (chosenTime  === 60) {
        init = "1 –º–∏–Ω—É—Ç–∞"
    }if (chosenTime  === 90) {
        init = "1:30"
    }if (chosenTime  === 120) {
        init = "2 –º–∏–Ω—É—Ç—ã"
    }

    return (
        <div className={"time-container"}>
            <div className={"time-block"}>
                {init}
            </div>
        </div>
    );
};
